      <button @click="newData()">Nuevo dato</button>

      <h3 v-for="(item, index) in customers" :key="index">
        {{ item.name }}
        <button @click="deleteData(item)">delete</button>
      </h3> -->
    </v-main>
  </v-app>
</template>

<script>
import { mapState, mapGetters, mapActions, mapMutations } from "vuex";
export default {
  name: "App",

  data: () => ({
    //
  }),
/*   created() {
    this.getItems();
    //this.newData();
  }, */
  computed: {
    ...mapState({
      customers: (state) => state.customers.customers,
    }),
  },
  methods: {
    ...mapActions({
      getItems: "customers/getAll",
      saveItem: "customers/save",
      deleteItem: "customers/delete",
    }),
    newData() {
      const dt = {
        id: this.serial(),
        name: "Cesar",
      };
      this.saveItem(dt);
      this.getItems();
    },
    deleteData(item) {
      this.deleteItem(item);
      this.getItems();
    },
    serial() {
      var d = new Date().getTime();
      var uuid = "xxxxxxxxxxxx4xxxyxxxxxxxxxxxxxxx".replace(
        /[xy]/g,
        function (c) {
          var r = (d + Math.random() * 16) % 16 | 0;
          d = Math.floor(d / 16);
          return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
        }
      );
      return uuid;
    },
  },
};
</script>
